import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from '@/common/constants';
import { getMyUserInfo } from "@/common/storageFunctions";
import { getUserinfo } from "@/common/requestFunctions";
/**
 * blindBox 盲盒界面
 * @description 盲盒界面
 * @Author: 穆兰
 * @Date: 2022/1/10
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/10
 */
export default {
    data() {
        return {
            boxSelected: 0,
            femaleBoxSum: 0,
            maleBoxSum: 0,
            lastClickTime: 0,
            boxTicketsAvailableSum: 0,
            userInfo: {},
            boxInfo: {},
            boxOwnerInfo: {}
        };
    },
    async onLoad() {
        this.userInfo = await getMyUserInfo();
        this.getBoxInfo();
        this.getBoxTicketsInfo(1);
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 点击查看盲盒规则
         */
        onShowRulesButtonClick() {
            this.$refs.boxRulePopup.open();
        },
        /**
         * @description 点击获取更多盲盒劵
         */
        onGetMoreTicketsClick() {
            uni.showModal({
                title: "获取方法",
                content: "投入盲盒即可获取一张盲盒劵哦~"
            });
        },
        /**
         * @description 点击放入盒子/我的盒子按钮
         */
        onPutBoxButtonClick() {
            if (this.boxSelected == 2) { //如果点击的是我的盒子
                uni.navigateTo({
                    url: "/pages/bugu-secondary-page/blind-box/my-box"
                });
                return;
            }
            this.$refs.putboxPopup.open();
        },
        /**
         * @description 确定投放盲盒
         */
        onPutBoxConfirm(e) {
            this.putBox(this.boxSelected, e);
        },
        /**
         * @description 点击剩余盲盒劵的文字
         */
        onBoxTicketsButtonClick() {
            uni.navigateTo({
                url: "/pages/bugu-secondary-page/blind-box/box-tickets"
            });
        },
        /**
         * @description 获取盲盒
         * @param {number} sex 性别 0-女 1-男
         */
        async takeBox(sex) {
            if (this.userInfo.sex == sex) {
                uni.showToast({
                    title: '只能获取异性的盲盒哦',
                    icon: 'error'
                });
                return;
            }
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'collect',
                    data: {
                        sex: sex
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                let boxInfo = res.data.data;
                let userInfo = await getUserinfo(boxInfo.userId);
                this.boxInfo = boxInfo;
                this.boxOwnerInfo = userInfo;
                this.getBoxInfo();
                this.$refs.takeboxPopup.open();
                console.log('takeBoxSuccess', res.data);
            }
            else {
                uni.showModal({
                    title: '获取失败',
                    content: res.data.userMsg
                });
                console.log('takeBoxFail', res.data);
            }
        },
        /**
         * @description  切换盲盒
         * @param {number} selected 选择的盒子
         */
        onBoxSelected(selected) {
            let clickTime = Date.now();
            if (this.boxSelected == selected) {
                if (this.boxSelected == 2) { //如果点击的是我的盒子
                    uni.navigateTo({
                        url: "/pages/bugu-secondary-page/blind-box/my-box"
                    });
                    return;
                }
                if (clickTime - this.lastClickTime < 500) {
                    return;
                }
                else {
                    this.lastClickTime = clickTime;
                }
                let _this = this;
                uni.showModal({
                    title: '抽取盲盒',
                    content: `你确定要消耗一张盲盒劵抽取一个${selected == 0 ? '女' : '男'}盒吗？`,
                    success: function (res) {
                        if (res.confirm) {
                            _this.takeBox(selected);
                            _this.lastClickTime = clickTime;
                        }
                        else if (res.cancel) {
                            console.log('用户点击取消');
                        }
                    }
                });
            }
            else {
                this.boxSelected = selected;
                this.lastClickTime = clickTime;
            }
        },
        /**
         * @description  投送盲盒
         * @param {number} sex 性别
         * @param {string} text 介绍的文字
         */
        async putBox(sex, text) {
            let res = await request({
                data: {
                    method: 'PUT',
                    group: 'blindBox',
                    action: 'deliver',
                    data: {
                        sex: sex,
                        text: text
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                this.getBoxInfo();
                uni.showToast({
                    title: '投送成功',
                    icon: 'success'
                });
            }
            else {
                console.log('putBoxFail', res.data);
            }
        },
        /**
         * @description  获取盲盒信息
         */
        async getBoxInfo() {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'info',
                    data: {},
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                this.femaleBoxSum = res.data.data.femaleBoxSum,
                    this.maleBoxSum = res.data.data.maleBoxSum;
            }
            else {
                console.log('getBoxInfo', res.data);
            }
        },
        /**
         * @description 获取盲盒劵信息
         * @param {number}  page 页号
         */
        async getBoxTicketsInfo(page) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'ticket/list',
                    data: {
                        page: page
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                this.boxTicketsAvailableSum = res.data.data.list.availableSum;
            }
            else {
                console.log('getBoxTicketsInfo', res.data);
            }
        },
    }
};
